@import url(https://fonts.googleapis.com/css?family=Lato&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
    margin: 0;
    padding: 0;
    border: 0;
    font: inherit;
    vertical-align: baseline;
}

:root{
    --main-font-color: #1a173b;
    --main-table-font-color: #354052;
    --light-font-color: #8a96a0;
    --lighter-font-color: #aab2c0;
    --white-font-color: #ffffff;

    --main-border-color: #ebedf8;

    --progress-bar-background-color: #c7cdd9;
    --progress-bar-bar-color: #0077ff;

    --main-background-color: #fafbfc;
    --white-background-color: #ffffff;

    --icons-idle-color: #C0C5D2;

    --main-blue-color: #0077ff;
    --main-pink-color: #f0166d;
}

html {
    font-family: 'Lato', Helvetica, sans-serif;
    font-weight: normal;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: normal;
    font-size: 10px;
}

.color-regular {
    color: #1a173b;
    color: var(--main-font-color);
}
.color-light {
    color: #8a96a0;
    color: var(--light-font-color);
}
.color-lighter {
    color: #aab2c0;
    color: var(--lighter-font-color);
}

h1, h2, h3, h4, h5, h6 {
    font-weight: bold;
    font-stretch: normal;
    font-style: normal;
    line-height: normal;
    letter-spacing: 0.07rem;
    color: #1a173b;
    color: var(--main-font-color);
}

h1 {
    font-size: 2.3rem;
}

h2 {
    font-size: 2rem;
    font-weight: normal;
}

h3 {
    font-size: 1.6rem;
    font-weight: normal;
}

p {
    font-size: 1.2rem;
}

.row {
    display: flex;
    flex-direction: row;
}

.column {
    display: flex;
    flex-direction: column;
}

